// import Cookie from 'js-cookie'
import { getCookie, setCookie, removeCookie } from '@/utils/auth'
import { loginAction, loginOut, /*getRoleByUserId,*/ getRolesListById } from '@/api/user'
const state = {
  session: getCookie(),
  name: '',
  avatar: '',
  introduction: '',
  roles: [],
  userId: '',
  roleObj: null
}

const mutations = {
  SET_SESSION: (state, session) => {
    state.session = session
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_INTRODUCTION: (state, instruction) => {
    state.instruction = instruction
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  },
  SET_USERID: (state, userId) => {
    state.userId = userId
  },
  SET_ROLEOBJ: (state, roleObj) => {
    state.roleObj = roleObj
  }
}

const actions = {
  doLogin({ commit }, loginForm) {
    const { userName, passWord } = loginForm
    const session = 'N2RmMmQ5MzUtYTEyOS00MTQ4LWEyOGUtZGEwYTlmMDU3OGRl'
    return new Promise((resolve, reject) => {
      loginAction({ userName: userName.trim(), passWord: passWord }).then(resp => {
        const { data } = resp
        commit('SET_SESSION', session)
        commit('SET_USERID', data.currentRole.id)
        setCookie('session',session)
        setCookie('userId', data.currentRole.id)
        resolve(resp)
      }).catch(error => {
        commit('SET_SESSION', '')
        commit('SET_ROLES', [])
        removeCookie('session')
        console.log('error')
        reject(error)
      })
    })
  },
  doUserInfo({ commit }, data) {
    //return new Promise((resolve, reject) => {
      // getRoleByUserId({ id: userId }).then(resp => {
        const roleObj = data.roles
        let roles = []
        roles = roles.concat(roleObj)
        commit('SET_ROLES', roles)
        commit('SET_ROLEOBJ', roleObj)
      //  resolve(resp)
      // }).catch(error => {
      //  console.log(`${error}`)
      //  reject(error)
      //})
    // })
  },
  doRolesListById({ commit }, roleObj) {
    const { page, size } = roleObj
    return new Promise((resolve, reject) => {
      getRolesListById({ page: page, size: size }).then(resp => {
        commit('SET_ROLES')
        resolve(resp)
      }).catch((error) => {
        console.log(`${error}`)
        reject(error)
      })
    })
  },
  doLoginOut({ commit }) {
    return new Promise((resolve, reject) => {
      loginOut().then(resp => {
        commit('SET_SESSION', '')
        commit('SET_ROLES', [])
        removeCookie('session')
        removeCookie('userId')
        removeCookie('role')
        removeCookie('data')
        resolve(resp)
      }).catch(error=> {
        console.log(`${error}`)
        reject(error)
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
