import axios from 'axios'
import { Message } from 'element-ui'
// import router from '@/router/index'

const service = axios.create({
  baseURL: process.env === 'production' ? 'http://192.168.20.75/v1' : process.env.VUE_APP_BASE_API,
  withCredentials: false,
  timeout: 5000
})
// request interceptor
service.interceptors.request.use(
  config => {
    console.log(process.env)
    if (config.method === 'post' && !config.url.includes('192.168.20.117:9001')) {
      const dataType = Object.prototype.toString.call(config.data) === '[object FormData]'
      if(dataType) {
        return config
      } else {
        const querystring = require('querystring')
        config.data = querystring.encode(config.data)
        
      }
    } else {
      return config;
    }
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  resp => {
    const ret = resp.data
    if (ret.status === 200) {
      return ret
    } else {
      return ret
    }
  },
  error => {    
    const { errorMessage } = JSON.parse(error.response.data.data.message).data[0]
    Message({
      message: errorMessage,
      type: 'error',
      duration: 3 * 1000
    })
    // router.push({path: '/login'})
    return Promise.reject(error)
  }
)

export default service
