<template>
  <div class="app-wrapper">
    <div class="app-navigation">
      <div class="web-siderbar-body">
        <div class="web-entry-logo">转移登记管理平台</div>
        <el-menu
          class="el-menu-custom"
          background-color="#ffffff"
          router
          mode="horizontal"
          @select="handleSelect"
        >
          <el-menu-item :route="null">帐号信息</el-menu-item>
          <el-menu-item>
            <router-link :to="{ path: '/updatePassword' }">修改密码</router-link>
          </el-menu-item>
          <el-menu-item class="select-menu-item" :route="null">
            <el-select
              @change="((val) => { roleChange(val) })"
              v-model="currentRole"
              placeholder="请选择"
            >
              <el-option
                v-bind:menuId="menu.id"
                v-for="menu in roleList"
                :key="menu.id"
                :label="menu.description"
                :value="menu.description"
              ></el-option>
            </el-select>
          </el-menu-item>
          <el-menu-item>
            <a href="#" @click="goLogin">退出</a>
          </el-menu-item>
        </el-menu>
      </div>
      <div class="web-navbar-body">
        <el-menu
          :default-active="activeMenu"
          router
          class="el-menu-navigation"
          background-color="transparent"
          active-text-color="#1890ff"
          text-color="#000"
        >
          <!--<sidebar-item v-for="route in this.menus" :key="route.id" :item="route" :base-path="route.path" />-->
          <el-menu-item
            v-for="item in this.menus"
            :index="resolvePath(item.path)"
            :route="{ path: item.path }"
            :key="item.id"
          >
            <i :class="item.icon"></i>
            <span slot="title">{{ item.text }}</span>
          </el-menu-item>
        </el-menu>
      </div>
      <div class="web-main-body">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: navObj.path }">{{ nav }}</el-breadcrumb-item>
        </el-breadcrumb>
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import path from "path";
import { getCookie, setCookie } from "@/utils/auth";
import { isExternal } from "@/utils/validate";
import { getChangeRole } from '@/api/user';
// import SidebarItem from "@/components/SidebarItem/SidebarItem.vue"
export default {
  name: "Layout",
  data() {
    return {
      currentRole: getCookie("role"),
      activeIndex: "1",
      nav: "",
      navObj: { path: "" },
      role: "",
      basePath: "",
      pathInfo: {
        发起业务: { id: 1001, path: "/business", icon: "el-icon-s-marketing" },
        待办事项: { id: 1002, path: "/schedule", icon: "el-icon-menu" },
        历史业务: { id: 1003, path: "/", icon: "el-icon-s-finance" },
        当前业务: { id: 1004, path: "/current", icon: "el-icon-picture" },
        权限管理: { id: 1005, path: "/jurManger", icon: "el-icon-s-marketing" },
        角色管理: { id: 1006, path: "/roleManger", icon: "el-icon-menu" },
        用户管理: { id: 1007, path: "/userManger", icon: "el-icon-menu" },
        开票信息录入: { id: 1001, path: "/invoiceBusiness", icon: "el-icon-s-marketing" }
      },
      adminPath: ["/jurManger", "/roleManger", "/userManger"],
      normalPath: ["/schedule", "/", "/current"],
      luruyuanPath: ["/business", "/schedule", "/", "/current"],
      menus: [
        { id: 1001, name: "", path: "", text: "", icon: "el-icon-s-marketing" },
        { id: 1002, name: "", path: "", text: "", icon: "el-icon-menu" },
        { id: 1003, name: "", path: "", text: "", icon: "el-icon-s-finance" },
        { id: 1004, name: "", path: "", text: "", icon: "el-icon-picture" }
      ]
    };
  },
  computed: {
    activeMenu() {
      const route = this.$route;
      const { meta, path } = route;
      // if set path, the sidebar will highlight the path you set
      if (meta.activeMenu) {
        return meta.activeMenu;
      }
      return path;
    },
    roleList: () => {
      return JSON.parse(sessionStorage.getItem("data")).roles;
    },
    ...mapState({
      roles: state => state.user.roles
    })
  },
  mounted() {
    const menus = JSON.parse(sessionStorage.getItem("menus"));
    
    const newMenus = menus.filter(item => {
        return item.description !== "开票信息录入"
    })
    this.menus = newMenus.map(item => {
      return {
        id: this.pathInfo[item.description].id,
        name: item.description,
        path: this.pathInfo[item.description].path,
        text: item.description,
        icon: this.pathInfo[item.description].icon
      };
    });
  },
  methods: {
    resolvePath(routePath) {
      if (isExternal(routePath)) {
        return routePath;
      }
      if (isExternal(this.basePath)) {
        return this.basePath;
      }
      return path.resolve("", routePath);
    },
    roleChange(val) {
      const roles = JSON.parse(sessionStorage.getItem('data')).roles
      const len = roles.length;
      for(let i = 0; i < len; i++) {
        if(roles[i].description === val) {
          getChangeRole({roleId: roles[i].id}).then(resp => {
            sessionStorage.setItem('menus', JSON.stringify(resp.data.currentRole.menus))
            setCookie('role', resp.data.currentRole.role)
            const menus = resp.data.currentRole.menus
            const newMenus = menus.filter(item => {
              return item.description !== '开票信息录入'
            })
            this.menus = newMenus.map(item => {
              return {
                id: this.pathInfo[item.description].id,
                name: item.description,
                path: this.pathInfo[item.description].path,
                text: item.description,
                icon: this.pathInfo[item.description].icon
              };
            });

            this.$router.push({path: '/'});  
          });
        }
      }
      
    },
    async goLogin() {
      await this.$store.dispatch("user/doLoginOut");
      this.$router.push({ path: "/login" });
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  watch: {
    currentRole: {
      handler(val) {
        return val
      },
      immediate: true
    },
    $route: {
      handler(to) {
        if (!to.meta.hasBread) {
          this.nav = "";
          this.navOj = {
            path: ""
          };
          return;
        }
        this.nav = to.meta.title;
        this.navObj = to;
      },
      immediate: true
    }
  }
};
</script>

<style lang='scss'>
.web-siderbar-body {
  height: 60px;
  position: fixed;
  width: 100%;
  background-color: #ffffff;
  box-shadow: 1px 1px 5px #ececec;
  z-index: 2;
  .el-select--medium .el-input--medium {
    width: 132px;
  }
  .select-menu-item:hover {
    background-color: #fff !important;
  }
  .el-input--medium > input {
    border: 0;
  }
  .web-entry-logo {
    color: #2f8ade;
    font-size: 17px;
    float: left;
    width: 200px;
    text-align: center;
    height: 60px;
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
  }
}
.web-main-body {
  position: relative;
  padding: 10px;
  margin-left: 210px;
  min-height: 380px;
  background-color: #fff;
  top: 70px;
  margin-right: 10px;
}
.el-menu-item i {
  position: relative;
  top: -1px;
  color: #000;
}
.web-navbar-body {
  position: fixed;
  top: 70px;
  left: 0;
  bottom: 0;
  min-width: 200px;
  background-color: #ffffff;
}
.app-wrapper {
  position: relative;
  height: 100%;
  width: 100%;

  &.mobile.openSidebar {
    position: fixed;
    top: 0;
  }
  .el-menu-custom {
    width: 442px;
    float: right;
    margin-right: 10px;
  }
  .el-menu-navigation {
    width: 200px;
    border-right: 0;
    li a {
      display: block;
      float: left;
      width: 100%;
    }
    li {
      padding: 0 !important;
    }
    li:hover {
      background-color: #ededed !important;
    }
  }
}
.el-menu-item.is-active {
  color: #2f8ade;
}
.drawer-bg {
  background: #000;
  opacity: 0.3;
  width: 100%;
  top: 0;
  height: 100%;
  position: absolute;
  z-index: 999;
}

.fixed-header {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9;
  transition: width 0.28s;
}

.hideSidebar .fixed-header {
  width: calc(100% - 54px);
}

.mobile .fixed-header {
  width: 100%;
}
</style>
